.filter-section {
    padding-top: 24px;
    padding-bottom: 24px;

    font-family: 'Manrope', sans-serif;
    font-weight: 400px;
    font-size: 12px;
    line-height: 1.3;

    @media screen and (min-width: 768px) {
        padding-top: 28px;
        padding-bottom: 28px;
    }
}

.filter-section__container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media screen and (min-width: 768px) {
        justify-content: center;
    }
}

.filter__list {
    display: none;

    @media screen and (min-width: 768px) {
        display: flex;  
        gap: 14px;
    }

    @media screen and (min-width: 1280px) {
        gap: 16px;
    }
}

.filter__item-desktop {
    display: none;

    @media screen and (min-width: 1280px) {
        display: block;
    }
}

.filter__btn {
    @media screen and (min-width: 768px) {
        padding: 0;
        color: $accent-color;
        background-color: transparent;
        border-radius: 20px;
        cursor: pointer;
    }
}

.filter__btn:hover,
.filter__btn:focus {
    color: $white-color;
    background-color: $accent-color;
}

.filter__btn-desktop {
    @media screen and (min-width: 768px) {
        color: $accent-color;
        background-color: transparent;
        border-color: $accent-color;
        border-radius: 20px;
        cursor: pointer;
    }
}

.filter__btn-desktop:hover,
.filter__btn-desktop:focus {
    color: $white-color;
    background-color: $accent-color;
}

/* The container <div> - needed to position the dropdown content */
.dropdown__categories {
    position: relative;
    display: inline-block;

    @media screen and (min-width: 768px) {
        display: none;
    }
}

.dropdown__others {
    display: none;

    @media screen and (min-width: 768px) {
        position: relative;
        display: inline-block;
    }
}

/* Show the dropdown menu on hover */
.dropdown__others:hover .dropdown__content {
    display: block;
}

.dropdown__categories:hover .dropdown__content {
    display: block;
}

.dropdown__others:hover .dropdown__content-mobile {
    display: block;
}

.dropdown__categories:hover .dropdown__content-mobile {
    display: block;
}

/* Dropdown Button */
.dropdown__btn {
    width: 112px;

    padding-top: 7.5px;
    padding-bottom: 7.5px;
    padding-left: 13px;
    padding-right: 37px;

    color: $accent-color;
    background-color: transparent;
    background: transparent;
    border: 1px solid $accent-color;
    border-radius: 20px;

    cursor: pointer;

    @media screen and (min-width: 768px) {
        width: 112px;

        padding-left: 24.5px;
        padding-right: 48.5px;

        margin-left: 14px;
    }

    @media screen and (min-width: 1280px) {
        margin-left: 16px;
    }
}

/* Change the background color of the dropdown button when the dropdown content is shown */
.dropdown__btn:hover,
.dropdown__btn:focus {
    color: rgba(244, 244, 244, 0.2);
    background-color: #4440F6;
}

.dropdown__btn-icon-arrow-down {
    position: absolute;
    top: 50%;
    right: 13px;
    transform: translateY(-50%);
    width: 14px;
    height: 14px;

    fill: $accent-color;

    @media screen and (min-width: 768px) {
        right: 24.5px;
    }
}

.dropdown__btn:focus+.dropdown__btn-icon-arrow-down,
.dropdown__btn:hover+.dropdown__btn-icon-arrow-down {
    display: none;
}

.dropdown__btn-icon-arrow-up {
    display: none;
}

.dropdown__btn:focus~.dropdown__btn-icon-arrow-up,
.dropdown__btn:hover~.dropdown__btn-icon-arrow-up {
    display: block;
    position: absolute;
    top: 50%;
    right: 13px;
    transform: translateY(-50%);
    width: 14px;
    height: 14px;

    fill: $white-color;

    @media screen and (min-width: 768px) {
        right: 24.5px;
    }
}

/* Dropdown Content (Hidden by Default) */
.dropdown__content {
    max-height: calc(100vh - 200px);
    overflow-y: auto;

    display: none;
    position: absolute;
    min-width: 148px;

    padding-top: 14px;
    padding-left: 14px;

    background-color: #F8F8F8;
    box-shadow: (0px 10px 60px rgba(0, 0, 0, 0.1));
    border-radius: 20px;
    z-index: 1;

    @media screen and (min-width: 320px) {
        min-width: 168px;
        left: 14px;
    }
}

.dropdown__content::-webkit-scrollbar {
    width: 12px;
}

.dropdown__content-mobile {
    max-height: calc(100vh - 200px);
    overflow-y: auto;

    display: none;
    position: absolute;
    min-width: 148px;

    padding-top: 14px;
    padding-left: 14px;

    background-color: #F8F8F8;
    box-shadow: (0px 10px 60px rgba(0, 0, 0, 0.1));
    border-radius: 20px;
    z-index: 1;

    @media screen and (min-width: 320px) {
        min-width: 168px;
    }
}

.dropdown__content-mobile::-webkit-scrollbar {
    width: 12px;
}
/* Links inside the dropdown */
.dropdown__category-btn {
    display: block;
    margin-bottom: 12px;
    color: #4440F7;
    background-color: transparent;
    border: none;
    cursor: pointer;
}

/* Change color of dropdown links on hover */
.dropdown__category-btn:hover,
.dropdown__category-btn:focus {
    text-decoration: underline;
}

