.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 37px;

    margin-left: auto;
}

.filter-section__text {
    display: none;

    @media screen and (min-width: 1280px) {
        display: block;
        font-weight: 700;
        font-size: 18px;
        line-height: 1.36;
        color:#111321;
    }
}

.filter-section__input-group {
    position: relative;
    margin-left: 24px;

    @media screen and (min-width: 768px) {
        margin-left: 40px;
    }

    @media screen and (min-width: 1280px) {
        margin-left: 34px;
    }
}

.datetime-picker__input {
    width: 151px;
    height: 31px;

    padding-left: 45px;

    color: #A2A2A2;
    font-family: 'Manrope', sans-serif;
    font-weight: 400;
    font-size: 12px;
    line-height: 1.75;

    border: 1px solid $primary-text-color-light-theme;
    border-radius: 20px;
    outline: 1px solid transparent;
    outline-offset: -1px;
    background-color: transparent;

    @media screen and (min-width: 768px) {
        width: 168px;
        font-size: 14px;
        line-height: 1.5;
    }
}

.datetime-picker__input:hover,
.datetime-picker__input:focus {
    color: $white-color;
    background-color: $accent-color;
    outline: 1px solid $accent-color;
    border-radius: 20px;
}

.datetime-picker__input-icon-calender {
    position: absolute;
    top: 50%;
    left: 12px;
    transform: translateY(-50%);
    width: 19px;
    height: 19px;

    fill: $accent-color;

    @media screen and (min-width: 768px) {
        width: 20px;
        height: 20px;
    }
}

.datetime-picker__input:focus + .datetime-picker__input-icon-calender,
.datetime-picker__input:hover + .datetime-picker__input-icon-calender {
    fill: $white-color;
}

.datetime-picker__input-icon-arrow-down {
    position: absolute;
    top: 50%;
    right: 11px;
    transform: translateY(-50%);
    width: 14px;
    height: 14px;

    fill: #A2A2A2;
}

.datetime-picker__input:focus ~ .datetime-picker__input-icon-arrow-down,
.datetime-picker__input:hover ~ .datetime-picker__input-icon-arrow-down {
    display: none;
}

.datetime-picker__input-icon-arrow-up {
    display: none;
}

.datetime-picker__input:focus ~ .datetime-picker__input-icon-arrow-up,
.datetime-picker__input:hover ~ .datetime-picker__input-icon-arrow-up {
    display: block;

    position: absolute;
    top: 50%;
    right: 11px;
    transform: translateY(-50%);
    width: 14px;
    height: 14px;

    fill: $white-color;
}

// FLATPICKR

.flatpickr-calendar {
    background-color: #F8F8F8;
    color: #000000;
    box-shadow: 0px 10px 60px rgba(0, 0, 0, 0.1);
    border-radius: 20px
}

.flatpickr-monthDropdown-months {
    font-family: 'Manrope', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.4;

    color: #000000;
}

.flatpickr-weekdays {
    font-family: 'Manrope', sans-serif;
    font-weight: 400;
    font-size: 12px;
    line-height: 1.5;
    color: rgba(60, 60, 67, 0.3);
}

.dayContainer {
    font-family: 'Manrope', sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.8;

    color: #000000;
}

.flatpickr-day.selected {
    background-color: $accent-color !important;
    border-color: $accent-color !important;
}

.selected:hover,
.selected:focus {
    background-color: $accent-color !important;
    border-color: $accent-color !important;
}

.flatpickr-prev-month {
    fill: $accent-color !important;
    stroke: $accent-color !important;
}

.flatpickr-next-month {
    fill: $accent-color !important;
    stroke: $accent-color !important;
}

